.station__name {
    margin: 10px;
    font-size: 1em;
}

.station-train-table {
    border-collapse: collapse;
    width: 100%;
    text-align: left;
}

.station-train-table__element {
    padding: 10px;
}

.station-train-table__element--centered {
    text-align: center;
}

.station-train-table th {
    background-color: #eaeaea;
}

.station-train-table__train {
    background-color: #eaeaea;
}

.station-train-table__train:first-child, .station-train-table__train:nth-child(2n+3) {
    background-color: white;
}

.station-train-link {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: black;
    border: 1px solid black;
    border-radius: 50%;
    width: 40px;
    height: 40px;
}
